    color: #222222;
    -webkit-margin-after: 9px;
            margin-block-end: 9px; }
    @media (min-width: 768px) and (max-width: 1024px), (min-width: 768px) and (max-width: 1024px) and (orientation: landscape), (min-width: 481px) and (max-width: 767px) {
