      .carousel-container .carousel .thumb {
        border: none;
        padding: 0;
        padding-bottom: 10px;
        margin-right: 10px;
        width: 70px; }
        .carousel-container .carousel .thumb.selected {
          border: none; }
          .carousel-container .carousel .thumb.selected img {
